.container-fluid {
  width: 100%;
}

.container {
  max-width: 1500px;
  margin: 0 auto;
}

[class^="col-"] {
  float: right;
}
.row {
  text-align: center;
}
.row::after {
  content: "";
  clear: both;
  display: table;
}
.responsive-image {
  width: 100%;
  height: auto;
  /* you can use this class for images */
}
